/**
 * Content
 */
.wp-site-blocks > * + *,
.entry-content {
  margin-top: 0 !important;
}

.wp-site-blocks {
  max-width: 1920px;
  margin: auto;
  border: 1px solid #999;
}

.wp-block-group > h2:first-child,
.wp-block-cover__inner-container > h2:first-child {
  margin-top: 0;
}

.wp-block-cover {
  align-items: flex-start;
}

.wp-block-image {
  margin-bottom: 0;
}

.wp-block-column {
  word-break: keep-all !important;
}

